import { combineReducers, configureStore } from "@reduxjs/toolkit";
import requestReducer from "./reducers/RequestSlice";
import pageReducer from "./reducers/PagesSlice";
var rootReducer = combineReducers({ requestReducer: requestReducer, pageReducer: pageReducer });
export var setupStore = function () {
    return configureStore({
        reducer: rootReducer,
    });
};
