import { createSlice } from "@reduxjs/toolkit";
import { fetchRequests } from "./ActionCreators";
var initialState = {
    requests: [],
    isLoading: false,
    error: "",
};
export var RequestSlice = createSlice({
    name: "request",
    initialState: initialState,
    reducers: {},
    extraReducers: function (builder) {
        builder.addCase(fetchRequests.fulfilled.type, function (state, action) {
            state.isLoading = false;
            state.error = "";
            state.requests = action.payload;
        }),
            builder.addCase(fetchRequests.pending.type, function (state) {
                state.isLoading = true;
            }),
            builder.addCase(fetchRequests.rejected.type, function (state, action) {
                state.isLoading = false;
                state.error = action.payload;
            });
    },
});
export default RequestSlice.reducer;
